export async function uploadToCloudinary(file: File): Promise<string> {
  console.log("üì§ Uploading to Cloudinary:", file.name, file.type, file.size);
  
  // Create form data
  const formData = new FormData();
  formData.append('file', file);
  formData.append('upload_preset', process.env.NEXT_PUBLIC_CLOUDINARY_UPLOAD_PRESET || 'your_upload_preset');
  formData.append('folder', 'team-members');
  formData.append('timestamp', Date.now().toString());
  
  try {
    // Upload to Cloudinary
    const response = await fetch(
      `https://api.cloudinary.com/v1_1/${process.env.NEXT_PUBLIC_CLOUDINARY_CLOUD_NAME || 'your_cloud_name'}/image/upload`,
      {
        method: 'POST',
        body: formData,
      }
    );

    if (!response.ok) {
      const errorData = await response.text();
      console.error('‚ùå Cloudinary upload failed:', response.status, errorData);
      throw new Error(`Upload failed: ${response.status} ${errorData}`);
    }

    const data = await response.json();
    console.log('‚úÖ Cloudinary response:', data);
    
    if (!data.secure_url) {
      throw new Error('No secure URL returned from Cloudinary');
    }

    return data.secure_url;
  } catch (error) {
    console.error('‚ùå Cloudinary upload error:', error);
    throw error;
  }
}